@font-face {
  font-family : "textile";
  src         : url("/assets/Textile.ttf");
}

body {
  margin           : 0;
  padding          : 0;
  background-color : black;
  font-family      : "textile";
}

input {
  font-family : "textile";
}

.app {
  display          : -ms-flexbox;
  display          : flex;
  height           : 100vh;
  background-image : url('https://i.imgur.com/rqzepn2.jpg');
  -ms-flex-line-pack    : center;
      align-content    : center;
  -ms-flex-pack  : center;
      justify-content  : center;
}

.input-field {
  display            : -ms-flexbox;
  display            : flex;
  height             : 363px;
  margin             : auto;
  padding            : 50px 100px;
  border-radius      : 12px;
  background-color   : #e6c51d;
  -ms-flex-direction     : column;
      flex-direction     : column;
  -ms-flex-pack    : center;
      justify-content    : center;
}

.test {
  color            : red;
  background-color : red;
}

.form {
  margin     : auto;
  -ms-flex-item-align : center;
      align-self : center;
}

.flex {
  display         : -ms-flexbox;
  display         : flex;
  -ms-flex-direction  : column;
      flex-direction  : column;
  -ms-flex-pack : center;
      justify-content : center;
}

.input-text {
  width : 100%;
}

.input {
  -webkit-box-sizing       : border-box;
          box-sizing       : border-box;
  width            : 100%;
  margin           : 8px 0;
  padding          : 12px 20px;
  color            : white;
  border           : 0;
  border-bottom    : 1px solid white;
  background-color : transparent;
}

.spacer {
  margin : 5px;
}

.input-text label {
  position   : relative;
  bottom     : 2.2rem;
  left       : 0;
  color      : #9e9e9e;
  font-size  : 1rem;
  -webkit-transition : 0.2s ease-out;
  -o-transition : 0.2s ease-out;
  transition : 0.2s ease-out;
  cursor     : text;
}

.input-text input[type="text"]:focus + label {
  bottom    : 4rem;
  color     : green;
  font-size : 0.75rem;
}

.input-text input[type="text"]:focus {
  border-bottom : 1px solid green;
  outline       : none;
  -webkit-box-shadow    : 0 1px 0 0 green;
          box-shadow    : 0 1px 0 0 green;
}

.input-text input[type="text"]:valid + label {
  bottom    : 4rem;
  color     : green;
  font-size : 0.75rem;
}

.column {
  min-width : 100px;
}

.button {
  display          : inline-block;
  padding          : 15px 32px;
  color            : white;
  border           : none;
  background-color : green;
  text-align       : center;
  text-decoration  : none;
  font-size        : 16px;
  cursor           : pointer;
}

.titleCard {
  display         : -ms-flexbox;
  display         : flex;
  width           : 100%;
  background      : black;
  -ms-flex-pack : center;
      justify-content : center;
}

.title {
  color      : white;
  -ms-flex-item-align : center;
      align-self : center;
  font-size  : 100px;
}

.row {
  display : -ms-flexbox;
  display : flex;
  width   : 400px;
  margin  : 0 auto;
}

.notification-stack {
  display        : -ms-flexbox;
  display        : flex;
  position       : absolute;
  -ms-flex-direction : column;
      flex-direction : column;
}

.inner {
  display        : -ms-flexbox;
  display        : flex;
  background     : transparent;
  font-size      : 16px;
  -ms-flex-direction : column;
      flex-direction : column;
}

.show {
  -webkit-transform : translateY(450px);
      -ms-transform : translateY(450px);
          transform : translateY(450px);
}

.notification {
  margin           : 4px;
  padding          : 15px 160px;
  color            : white;
  opacity          : 2.4;
  border           : none;
  border-radius    : 4px;
  background-color : red;
  text-align       : center;
  text-decoration  : none;
  -webkit-transition       : 0.3s ease-out;
  -o-transition       : 0.3s ease-out;
  transition       : 0.3s ease-out;
}

.container {
  display         : -ms-flexbox;
  display         : flex;
  position        : relative;
  width           : 100%;
  height          : 0;
  padding-bottom  : 56.25%;
  -ms-flex-pack : center;
      justify-content : center;
}

.player {
  width  : 90vw;
  height : 90vh;
  margin : auto 0;
}

iframe {
  border-width : 0px;
}



